import React from 'react';
import App from './App';
import AppsLoader from '../components/AppsLoader';

const AppsList = ({apps, page, pageLimit, isLoading}) => {
  let list = [];

  // Render content loader if loading
  if (isLoading) {
    for (let i = 0; i < pageLimit; i++) {
      list[i] = (
        <li key={i}>
          <AppsLoader />
        </li>
      )
    }
  } else {
    // Get apps in a specific page
    const pagedApps = apps.slice((page - 1) * pageLimit, page * pageLimit);

    list = pagedApps.map(app => (
      <li key={app.id}>
        <App app={app} />
      </li>
    ));
  }

  return <ul>{list}</ul>;
};

export default AppsList;
