import React from 'react';

const EmptyListMessage = ({apps, appSearchTerm, currentCategory, isLoading}) => {
  // Hide component if not an empty list or if list is loading for the first time
  if (apps.length || isLoading) {
    return null;
  }

  let message = ['No results found'];

  if (appSearchTerm) {
    message.push(' for ', <em key="1">"{appSearchTerm}"</em>);
  }

  if (currentCategory) {
    message.push(' in ', <strong key="2">{currentCategory}</strong>);
  }

  message.push('.');

  return <span>{message}</span>;
};

export default EmptyListMessage;
