import React, {Component} from 'react';
import AppsList from '../components/AppsList';
import Pagination from '../components/Pagination';
import SearchBar from '../components/SearchBar';
import Categories from '../components/Categories';
import EmptyListMessage from '../components/EmptyListMessage';
import StatusMessage from '../components/StatusMessage';

const PAGE_LIMIT = 3;

class AppsListContainer extends Component {
  constructor(props) {
    super(props);

    this.state = {
      apps: [], // Original apps list
      appsFound: [], // Filtered apps list
      page: 1,
      currentCategory: '',
      appSearchTerm: '',
      isLoading: false,
      error: null
    };

    this.handleAppSearch = this.handleAppSearch.bind(this);
    this.handleCategoryFilter = this.handleCategoryFilter.bind(this);
  }

  componentDidMount() {
    this.setState({isLoading: true});
    // apps.json file hosted in github pages
    fetch('https://tiagoportfolio.github.io/data/apps.json')
      .then(response => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error('Something went wrong...');
        }
      })
      .then(data => {
        const sortedApps = this.sortApps(data);
        this.setState({
          apps: sortedApps,
          appsFound: sortedApps,
          isLoading: false
        });
      })
      .catch(error => this.setState({error, isLoading: false}));
  }

  // Filter apps by search term and category
  filterApps = (apps, appSearchTerm, category) =>
    apps.filter(
      app =>
        (appSearchTerm
          ? app.name.toLowerCase().includes(appSearchTerm)
          : true) && (category ? app.categories.includes(category) : true)
    );

  // Sort by ascending order of the sum of the plans price
  sortApps = apps =>
    apps.sort(
      (a, b) =>
        a.subscriptions.reduce((total, {price}) => total + price, 0) -
        b.subscriptions.reduce((total, {price}) => total + price, 0)
    );

  handleCategoryFilter = category => {
    this.setState((state, props) => {
      const currentCategory =
        category === state.currentCategory ? '' : category;
      return {
        appsFound: this.filterApps(
          state.apps,
          state.appSearchTerm,
          currentCategory
        ),
        currentCategory,
        page: 1
      };
    });
  };

  handleAppSearch = e => {
    const appSearchTerm = e.target.value.toLowerCase();
    this.setState((state, props) => ({
      appsFound: this.filterApps(
        state.apps,
        appSearchTerm,
        state.currentCategory
      ),
      appSearchTerm,
      page: 1
    }));
  };

  handlePagination = page => {
    this.setState({page});
  };

  render() {
    const {
      appsFound,
      page,
      appSearchTerm,
      currentCategory,
      isLoading,
      error
    } = this.state;

    return (
      <div className="flex-container">
        <Categories
          filterCategory={this.handleCategoryFilter}
          currentCategory={currentCategory}
        />
        <section className="apps-list">
          <SearchBar
            searchApp={this.handleAppSearch}
            appSearchTerm={appSearchTerm}
          />
          <StatusMessage error={error}>
            <AppsList apps={appsFound} page={page} pageLimit={PAGE_LIMIT} isLoading={isLoading}/>
            <EmptyListMessage
              apps={appsFound}
              appSearchTerm={appSearchTerm}
              currentCategory={currentCategory}
              isLoading={isLoading}
            />
          </StatusMessage>
          <Pagination
            page={page}
            pageLimit={PAGE_LIMIT}
            totalApps={appsFound.length}
            changePage={this.handlePagination}
          />
        </section>
      </div>
    );
  }
}

export default AppsListContainer;
